<template>
  <div></div>
</template>

<script setup>
//导入pixi
import * as PIXI from 'pixi.js'
// import { original } from './assets/original.js'
// import { ShockwaveFilter } from 'pixi-filters'
//创建应用
const app = new PIXI.Application({
  width: window.innerWidth,
  height: window.innerHeight,
  backgroundColor: 0xffffff,
  resolution: window.devicePixelRatio || 1,//设备像素比例
  antialias: true,//抗锯齿
})
//添加画布
document.body.appendChild(app.view)

//创建容器
const container = new PIXI.Container()
// container.scale.set(1.5)//缩放
//将容器添加到舞台
app.stage.addChild(container)

// 屏幕
const screenWidth = app.screen.width
const screenHeight = app.screen.height
//地面位置
const groundY = screenHeight / 1.3


// 添加恐龙游戏的精灵纹理 1233 x 100|2446 x 194
const baseTexture = PIXI.BaseTexture.from("./textures/dino2.png")

// 创建恐龙纹理  //用一个矩形截取
const dinoTexture = new PIXI.Texture(baseTexture, new PIXI.Rectangle(75, 8, 88, 88))
//创建恐龙精灵
const dinoSprite = new PIXI.Sprite(dinoTexture)
dinoSprite.scale.set(1.5)//缩放
dinoSprite.position.set(screenWidth / 2 - 88, groundY - 88)
container.addChild(dinoSprite)

//恐龙跑步纹理
const runTextures = []
for (let i = 0; i < 2; i++) {
  runTextures.push(
    new PIXI.Texture(baseTexture,
      new PIXI.Rectangle(1682 + (i + 2) * 88, 6, 81, 87)
    ))
}
//恐龙跑步动画
const runAnimation = new PIXI.AnimatedSprite(runTextures)
runAnimation.animationSpeed = 0.1
runAnimation.play()
runAnimation.visible = false
runAnimation.x = 60
runAnimation.y = groundY - 100
runAnimation.scale.set(1.5)//缩放
container.addChild(runAnimation)

//恐龙冲刺纹理
const runFastTexture = []
for (let i = 0; i < 2; i++) {
  runFastTexture.push(
    new PIXI.Texture(baseTexture,
      new PIXI.Rectangle(2210 + (i + 0) * 118, 40, 110, 52)
    ))
}
//恐龙冲刺动画
const sprintAnimation = new PIXI.AnimatedSprite(runFastTexture)
sprintAnimation.animationSpeed = 0.1
sprintAnimation.play()
sprintAnimation.visible = false
sprintAnimation.x = 60
sprintAnimation.y = groundY - 52
sprintAnimation.scale.set(1.5)//缩放
container.addChild(sprintAnimation)

//恐龙跳跃精灵
const jumpTexture = new PIXI.Texture(baseTexture, new PIXI.Rectangle(1682 + 0 * 88, 6, 81, 87))
const jumpSprite = new PIXI.Sprite(jumpTexture)
jumpSprite.visible = false
jumpSprite.x = 60
jumpSprite.y = groundY - 88
jumpSprite.scale.set(1.5)//缩放
container.addChild(jumpSprite)

//恐龙死亡精灵
const crashedTexture = new PIXI.Texture(baseTexture, new PIXI.Rectangle(1682 + 1 * 88, 6, 81, 87))
const crashedSprite = new PIXI.Sprite(crashedTexture)
crashedSprite.visible = false
crashedSprite.scale.set(1.5)//缩放
container.addChild(crashedSprite)

//地面精灵
const groundTexture = new PIXI.Texture(baseTexture, new PIXI.Rectangle(2, 104, 2400, 22))
const groundSprite = new PIXI.TilingSprite(groundTexture)
groundSprite.width = screenWidth
groundSprite.height = 22
groundSprite.position.set(0, groundY)
groundSprite.scale.set(1.5)//缩放
container.addChild(groundSprite)

//创建仙人掌精灵
const cactusTexture = new PIXI.Texture(baseTexture, new PIXI.Rectangle(448, 4, 30, 66))
const cactusSprite = new PIXI.Sprite(cactusTexture)
cactusSprite.position.set(screenWidth, groundY - 66)
cactusSprite.scale.set(1.5)//缩放
container.addChild(cactusSprite)

//创建文字
const starText = new PIXI.Text('开始游戏', {
  fontFamily: 'Arial',
  fontSize: 44,
  fill: '#222',
  align: 'center',
})
starText.anchor.set(0.5, 0.5)
starText.position.set(screenWidth / 2, screenHeight / 3)
container.addChild(starText)
starText.interactive = true
starText.on('click', () => {
  const timer = setInterval(() => {
    dinoSprite.x -= 15
    if (dinoSprite.x < 0) {
      dinoSprite.visible = false
      dinoSprite.x = 100
      clearInterval(timer)
      playGame()
    }
  }, 10)


})

//得分数字纹理
const scoreTextTexture = []
for (let i = 0; i < 10; i++) {
  scoreTextTexture.push(
    // new PIXI.Sprite(
    new PIXI.Texture(baseTexture,
      new PIXI.Rectangle(1294 + i * 20, 2, 18, 21)))
  // )
}
//数字动画
const numberAnimation = new PIXI.AnimatedSprite(scoreTextTexture)
numberAnimation.animationSpeed = 0.1
numberAnimation.visible = false
numberAnimation.x = screenWidth / 1.5
numberAnimation.y = screenHeight / 3
container.addChild(numberAnimation)

//游戏状态
let isGameing = false
let isGameover = false
//得分
let score = 0
//初始跳跃速度
let jumpVelocity = 9
//初始化重力加速度
let gravity = 0.2
//游戏速度
let gameSpeed = 5
let speedup = gameSpeed + 5
let speedDown = gameSpeed - 3
let tempSpeed = gameSpeed
//恐龙状态
// let isJumping = false
// let isDead = false

//开始游戏
function playGame() {
  isGameing = true
  isGameover = false
  numberAnimation.visible = true
  numberAnimation.gotoAndPlay(0)
  //重置障碍位置
  cactusSprite.position.set(screenWidth + Math.random() * 100, groundY - 66)
  runAnimation.visible = true
  console.log('开始游戏');
}
//增加分位数
// function addScore() {
  
// }
// 游戏循环
app.ticker.add(() => {
  if (isGameover) return
  if (isGameing) {
    //隐藏文字
    starText.visible = false
    //地面移动
    groundSprite.tilePosition.x -= gameSpeed / 1.5 //与缩放倍数有关
    //仙人掌移动
    cactusSprite.x -= gameSpeed
    //成功避开障碍
    if (cactusSprite.x <= -30) {
      // 重置仙人掌
      cactusSprite.x = screenWidth + Math.random() * 300
      // 增加分数
      score += 10
    }
  }
  //跳跃动画
  if (jumpSprite.visible) {
    //降速上升 负数下降
    // 跳跃速度
    jumpVelocity -= gravity
    //更新跳跃高度
    jumpSprite.y -= jumpVelocity
    //回到地面
    if (jumpSprite.y + 88 >= groundY) {
      jumpSprite.visible = false
      jumpSprite.y = groundY - 88
      jumpVelocity = 9
      //跳跃结束 重置状态
      // iskeydown = true
      gravity = 0.2
      // isJumping = false
      if (!iskeydownDown) {
        runAnimation.visible = true
      } else {
        sprintAnimation.visible = true
      }
    }
  }
  //检测碰撞
  // 龙 y轴需大于原始y - 自身高度
  // 龙 x轴需在障碍范围内 
  // 龙 原始x + 自身宽度  大于 障碍x
  // 龙 原始x - 障碍宽度 小于 障碍x
  if (jumpSprite.y > cactusSprite.y - 88 &&
    jumpSprite.x > cactusSprite.x - 87 &&
    jumpSprite.x < cactusSprite.x + 30
    // && !'0'
  ) {
    numberAnimation.stop()
    gameOver()
  }

  // 更新分数
  // if(numberAnimation.currentFrame === 10){

  // }
})


function gameOver() {
  isGameover = true
  const endText = new PIXI.Text('游戏结束了\n得分:' + score, {
    fontFamily: 'Arial',
    fontSize: 44,
    fill: '#222',
    align: 'center',
  })
  endText.anchor.set(0.5)
  endText.position.set(screenWidth / 2, screenHeight / 3)
  container.addChild(endText)
  endText.interactive = true
  endText.on('click', () => {
    endText.visible = false
    starText.visible = true
  })
  score = 0
}

// //跳跃 是否大跳
// function jump(type = 0) {
//   if (isJumping) return
//   console.log(gravity, jumpVelocity);
//   if (type) {
//     jumpVelocity = 25
//   } else {
