//   }
//   isJumping = true
//   runAnimation.visible = false
//   sprintAnimation.visible = false
//   jumpSprite.visible = true
// }

// //按下 space
// let iskeydownSpace = false
// //按下 时间
// let iskeydownTime = 0
// //按下 down
let iskeydownDown = false
// //按下 right
// let iskeydownRight = false

window.addEventListener('keydown', (e) => {
  switch (e.code) {
    case 'Space'://跳
      jumpSprite.visible = true
      runAnimation.visible = false
      sprintAnimation.visible = false
      break;
    case 'ArrowUp':
      jumpSprite.visible = true
      runAnimation.visible = false
      sprintAnimation.visible = false
      break;
    case 'ArrowDown'://下落 蹲
      if (jumpSprite.visible) {
        gravity = 0.5
      } else {
        iskeydownDown = true
        runAnimation.visible = false
        sprintAnimation.visible = true
      }
      break;
    case 'ArrowRight'://右 加速
      gameSpeed = speedup
      runAnimation.animationSpeed = 0.3
      sprintAnimation.animationSpeed = 0.3
      break;
    case 'ArrowLeft'://左 减速
      gameSpeed = speedDown
      runAnimation.animationSpeed = 0.03
      sprintAnimation.animationSpeed = 0.03
      break;
  }
})

window.addEventListener('keyup', (e) => {
  switch (e.code) {
    case 'Space' || 'ArrowUp'://跳
      // jumpSprite.visible = true
      // iskeydownSpace = false
      break;
    case 'ArrowDown'://下
      if (jumpSprite.visible) {
        gravity = 1
      } else {
        iskeydownDown = false
        runAnimation.visible = true
        sprintAnimation.visible = false
      }
      break;
    case 'ArrowRight'://右
      gameSpeed = tempSpeed
      runAnimation.animationSpeed = 0.1
      sprintAnimation.animationSpeed = 0.1
      break;
    case 'ArrowLeft'://左 减速
      gameSpeed = tempSpeed
      runAnimation.animationSpeed = 0.1
      sprintAnimation.animationSpeed = 0.1
      break;
  }
})


